import React, { useState, useEffect } from 'react';
import WaterTemp from './WaterTemp';
import AirTemp from './AirTemp';
import WindDirection from './WindDirection';
import Sunset from './Sunset';
import BuoyReport from './BuoyReport';
//import BuoyData from './BuoyData';
//import LuecadiaRSS from './LeucadiaRSS';
import CollapseToggleButton from '../utils/CollapseToggleButton';
import BuoysDisplay from './BouysDisplay';
import icons from '../utils/icons';
import tide from '../../assets/images/tide.png';
import getDirection from './getDirection';

const ConditionsDashboard = ({
    tideDisplay, 
    setWind,
    status,
    setStatus
}) => {

    const getLocalData = (localItem) => localStorage.getItem(localItem);
    const collapseStateInit = (localItem) => getLocalData(localItem) ? getLocalData(localItem) === 'true' : true;
    const heightInit = () => getLocalData('height') ? getLocalData('height') : '';
    const getCurrentTide = (heightInit() > 4) ? "high" : (heightInit() < 2) ? "low" : "medium";
    
    const [tideCollapse, setTideCollapse] = useState(collapseStateInit('tideCollapse'));
    const [conditionCollapse, setConditionCollapse] = useState(collapseStateInit('conditionCollapse'));
    const [buoyCollapse, setBuoyCollapse] = useState(collapseStateInit('buoyCollapse'));
    console.log(`ConditionsDashboard => status: ${JSON.stringify(status, null, 2)}`)
    
    useEffect(() => {
        localStorage.setItem('tideCollapse', tideCollapse);
    }, [tideCollapse]);
    useEffect(() => {
        localStorage.setItem('conditionCollapse', conditionCollapse);
    }, [conditionCollapse]);
    useEffect(() => {
        localStorage.setItem('buoyCollapse', buoyCollapse);
    }, [buoyCollapse]);

    const windDisplay = () => <WindDirection columns='2' setWind={setWind} height='0px' collapse={conditionCollapse}/>
    const windHeader = () => <div>{icons.wind} {status.windDirection} {status.windGusts}<span className='size12'>mph</span> {icons.water}{localStorage.getItem('waterTemp')}°<span className='size12'>F</span> {icons.temperature} {localStorage.getItem('airTemp')}°<span className='size12'>F</span></div>
    return (
        <div className=''>
            <div className='containerBox bold color-yellow bg-lite p-20'>
                <CollapseToggleButton
                    //title={`TIDE ${icons.moon}`}
                    title={`TIDE ${Number(heightInit()).toFixed(0)}ft ${getCurrentTide}`}
                    isCollapsed={tideCollapse}
                    setCollapse={setTideCollapse}
                    align='left'
                    icon={tide}
                />
            </div>
            {
                (tideCollapse) 
                ? <div></div> 
                : <div className='containerBox'>
                    <div className='containerBox'>
                        {tideDisplay('wide')}
                    </div>
                </div>
            }
            <div className='containerBox bold color-yellow bg-lite p-20'>
                <CollapseToggleButton
                    title={''}
                    component={windHeader()}
                    isCollapsed={conditionCollapse}
                    setCollapse={setConditionCollapse}
                    align='left'
                />
            </div>
            <div className={`${(conditionCollapse)?' hidden ht-0 mb--5':'containerBox'}`}>
                <div className='flexContainer'>
                    <div className='containerBox flex2Column'>
                        <div className='containerBox bold color-yellow'>
                            WIND {icons.wind}
                        </div>
                        {windDisplay()}
                    </div>
                    <div className='containerBox flex2Column'>
                        <div className='containerBox bold color-yellow'>
                            WATER {icons.water}
                        </div>
                        <WaterTemp
                            setStatus={setStatus}
                        />
                        <div className='containerBox bold color-yellow'>
                            AIR {icons.temperature}
                        </div>
                        <AirTemp/>
                    </div>
                </div>
            </div>
            
            <Sunset view='full'/>
            <div className='containerBox bold color-yellow bg-lite p-20'>
                <CollapseToggleButton
                    title={`${icons.buoys} BUOYS`}
                    isCollapsed={buoyCollapse}
                    setCollapse={setBuoyCollapse}
                    align='left'
                />
            </div>
            {
                (buoyCollapse)
                ? null
                : <BuoysDisplay />
                /*<BuoyReport />*/
                /*<BuoyData />*/
                /*<LuecadiaRSS/>*/
            }
        </div>
    )
}

export default ConditionsDashboard;